import BaseApiService from "services/BaseApiService";

const DEFAULT_PATH = "/custom/api";
const DEFAULT_PATH_DEDUPE = "/custom/api/dedupe";

class DedupeService extends BaseApiService {
  /*-------------------- Customer Dedupe------------------------- */

  getApplicants = (search: string) => {
    const url = `${DEFAULT_PATH}/applicant/search?${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  getApplicantsKyc = (search: string) => {
    const url = `${DEFAULT_PATH}/kyc/search?${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  getCustomerForDedupe = (start = 0, limit = 10, search: string) => {
    const url = `${DEFAULT_PATH_DEDUPE}/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  getCustomerKycForDedupe = (start = 0, limit = 10, search: string) => {
    const url = `${DEFAULT_PATH_DEDUPE}/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  getCustomerDedupeStatus = (search: string) => {
    const url = `${DEFAULT_PATH_DEDUPE}/search?${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  searchCustomerForDedupe = (payload: any) => {
    const url = `${DEFAULT_PATH}/manage/dedupe/customer/search`;
    return this.makePostRequestWithAuth(url, payload);
  };

  searchCustomerKycForDedupe = (payload: any) => {
    const url = `${DEFAULT_PATH}/manage/dedupe/kyc/search`;
    return this.makePostRequestWithAuth(url, payload);
  };

  /*----------------------Address Dedupe--------------------------*/

  getAddress = (search: string) => {
    const url = `${DEFAULT_PATH}/property/address/search?${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  getAddressForDedupe = (start = 0, limit = 10, search: string) => {
    const url = `${DEFAULT_PATH_DEDUPE}/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  getAddressDedupeStatus = (search: string) => {
    const url = `${DEFAULT_PATH_DEDUPE}/search?${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  searchAddressForDedupe = (payload: any) => {
    const url = `${DEFAULT_PATH}/manage/dedupe/address/search`;
    return this.makePostRequestWithAuth(url, payload);
  };

  /*------------------------Property Loan Linking Dedupe-------------------- */

  getLinkedAddress = (start = 0, limit = 10, search: string) => {
    const url = `${DEFAULT_PATH}/manage/dedupe/address/linked?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  getPropertyLinkageStatus = (search: string) => {
    const url = `${DEFAULT_PATH_DEDUPE}/search?${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  getSuggestionsForPropertyLinkSearch = (start = 0, limit = 10, search: string) => {
    const url = `${DEFAULT_PATH}/manage/dedupe/address/linkage/${search}?start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(url);
  };

  /*------------------------Customer Loan Linking Dedupe-------------------- */

  getLinkedCustomer = (start = 0, limit = 10, search: string) => {
    const url = `${DEFAULT_PATH}/manage/dedupe/customer/linked?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  getSuggestionsForCustomerLinkSearch = (start = 0, limit = 10, search: string) => {
    const url = `${DEFAULT_PATH}/manage/dedupe/customer/linkage/${search}?start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(url);
  };

  getCustomerLinkageStatus = (search: string) => {
    const url = `${DEFAULT_PATH_DEDUPE}/search?${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  /*--------------------------------------------------- */

  updateDedupe = (payload: any) => {
    const url = `${DEFAULT_PATH}/manage/dedupe`;
    return this.makePutRequestWithAuth(url, payload);
  };

  updateDedupeMultiple = (payload: any) => {
    const url = `${DEFAULT_PATH}/manage/dedupe/multiple`;
    return this.makePutRequestWithAuth(url, payload);
  };

  updateDedupeFinalStatus = (payload: any) => {
    const url = `${DEFAULT_PATH}/manage/dedupe`;
    return this.makePutRequestWithAuth(url, payload);
  };

  stageComplete = (uid: string, payload: any) => {
    const url = `${DEFAULT_PATH}/manage/dedupe/complete/${uid}`;
    return this.makePutRequestWithAuth(url, payload);
  };
}

export default new DedupeService();
